<div class="container-fluid"><div class="companyPageMain row"><div class="col-12 col-md-4"><div class="companyPageInfo"><h3>{{company.name}}</h3><div class="form-group"><label class="boldText">О компании</label><p class="companyPageDescription">{{company.description}}</p></div><div class="form-group"><label class="boldText">Сайт</label><br><a :href="company.site">{{company.site}}</a></div><div class="form-group"><label class="boldText">GROBAL_ID</label><p class="companyPageDescription">{{company.id}}</p></div></div></div><div class="col-12 col-md-8"><div class="companyPageMatch"><div class="matchHead"><h3>Данные о компании</h3><div class="notifWarn" v-if="company.id_rek_sub_ind.length || company.id_rek_tech_3.length">Исправьте несоответствия</div></div><div class="form-group"><p>Источник парсинга -<a :href="company.site">{{company.site}}</a></p></div><div class="matchTitles row"><div class="col-3">Исходные данные</div><div class="col-3">Найденные несоответствия</div><div class="col-3">Данные с парсера (Бета)</div><div class="col-3">Статус</div></div><div class="matchMistake"><VisitorCompanyMatchItem @changeData="$_vision_company_index_savedChanges($event, 'id_ind')" :field="company.id_ind" :rekField="company.id_rek_sub_ind[0]" :parser="company.id_parser_sub_ind[0]" name="Отрасль"></VisitorCompanyMatchItem><VisitorCompanyMatchItem @changeData="$_vision_company_index_savedChanges($event, 'id_sub_ind')" :field="company.id_sub_ind" :rekField="company.id_rek_sub_ind[1]" :parser="company.id_parser_sub_ind[1]" name="Подотрасль"></VisitorCompanyMatchItem><VisitorCompanyMatchItem @changeData="$_vision_company_index_savedChanges('id_tech_1')" :field="company.id_tech_1" :rekField="company.id_rek_tech_3[0]" :parser="company.id_parser_tech_3[0]" name="Технология (1 уровень)"></VisitorCompanyMatchItem><VisitorCompanyMatchItem @changeData="$_vision_company_index_savedChanges" :field="company.id_tech_2" :rekField="company.id_rek_tech_3[1]" :parser="company.id_parser_tech_3[1]" name="Технология (2 уровень)"></VisitorCompanyMatchItem><VisitorCompanyMatchItem @changeData="$_vision_company_index_savedChanges" :field="company.id_tech_3" :rekField="company.id_rek_tech_3[2]" :parser="company.id_parser_tech_3[2]" name="Технология (3 уровень)"></VisitorCompanyMatchItem></div></div><div><button class="btnSecondary">Заменить все исходные данные на найденные</button></div><div class="companyProducts"><div class="matchHead"><h3>Продукты компании</h3></div><VisitorCompanyCard v-for="prod in products" :company="prod"></VisitorCompanyCard></div></div></div></div>